@import 'fonts';

:root {
  --color-orange: #edb32d;
  --color-yellow: #f0e714;
  --color-white: #ffffff;
  --color-gray: #bab9b9;
  --color-btn: #cecece;
  --color-main-title: #f3f4f6;
  --color-yellow-second: rgba(240, 231, 20, 0.502);
  --color-drop-shadow: rgba(119, 134, 75, 0.149);
  --color-dark: #101c26;
  --color-section-title: #24272e;
  --color-section-parag: #555c68;
  --color-section-box: rgba(240, 231, 20, 0.2);
}

.wrapper {
  width: 100%;
  min-height: 100%;
  overflow: hidden;
  font-size: 15px;
}

.container {
  max-width: 1298px;
  margin: 0 auto;
  padding: 0 10px;
  height: 100%;
}
.image--border {
  border-radius: 10px;
  img {
    border-radius: 10px;
  }
}
.section--box {
  color: var(--color-section-title);
  font-size: calc(13px + 3 * ((100vw - 320px) / 1298));
  font-weight: 400;
  background-color: var(--color-section-box);
  border-radius: 1000px;
  padding: 10px 16px;
  text-transform: uppercase;
  display: inline-block;
  margin-bottom: 24px;
}

.section--title {
  color: var(--color-section-title);
  font-size: calc(18px + 19 * ((100vw - 320px) / 1298));
  font-weight: 600;
  line-height: 1.1;
  margin-bottom: 16px;
}

.section--text {
  color: var(--color-section-parag);
  font-weight: 400;
  font-size: calc(15px + 5 * ((100vw - 320px) / 1298));
  line-height: 1.3;
}

.button {
  display: flex;
  justify-content: center;
  align-items: center;
  background: var(--color-section-title);
  padding: 15px 30px;
  font-size: 15px;
  font-weight: 600;
  color: var(--color-yellow);
  gap: 6px;
  border-radius: 100px;
  @media (max-width: 740px) {
    padding: 10px 18px;
  }
}

.burger {
  display: none;
  @media (max-width: 800px) {
    margin-right: 10px;
    background: var(--color-yellow);
    box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset,
      rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;
    top: 25px;
    left: 20px;
    z-index: 150;
    height: 40px;
    width: 40px;
    border-radius: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: all 0.3s;
    span {
      display: block;
      width: 25px;
      height: 3px;
      background-color: #fff;
      border-radius: 3px;
      margin: 4px 0;
    }
    &::before,
    &::after {
      content: '';
      display: block;
      width: 25px;
      height: 3px;
      background-color: #fff;
      border-radius: 3px;
    }
  }
  &.burger__active {
    span {
      display: none;
    }
    &.burger::before {
      position: absolute;
      transform: rotate(45deg);
      box-shadow: 0 6px 0 rgba(0, 0, 0, 0);
      transition: top 0.3s, -webkit-box-shadow 0.15s, -webkit-transform 0.3s;
      transition: box-shadow 0.15s, top 0.3s, transform 0.3s;
      transition: box-shadow 0.15s, top 0.3s, transform 0.3s,
        -webkit-box-shadow 0.15s, -webkit-transform 0.3s;
    }
    &.burger::after {
      position: absolute;
      transform: rotate(-45deg);
      transition: top 0.3s, -webkit-box-shadow 0.15s, -webkit-transform 0.3s;
      transition: box-shadow 0.15s, top 0.3s, transform 0.3s;
      transition: box-shadow 0.15s, top 0.3s, transform 0.3s,
        -webkit-box-shadow 0.15s, -webkit-transform 0.3s;
    }
  }
}

.header {
  background-color: var(--color-dark);
  height: 100vh;
  display: flex;
  flex-direction: column;
  background-image: url(..//images/decor/header-bg.svg);
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover;

  @media (max-width: 904px) {
    height: 100%;
    min-height: 100vh;
  }
}
.header__top {
  @media (max-width: 800px) {
    position: fixed;
    width: 100%;
    z-index: 100;
    background-color: var(--color-dark);
  }
}
.header__top-inner {
  padding: 27px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
  @media (max-width: 800px) {
    justify-content: space-between;
  }
}
.header__top-logo {
  @media (max-width: 800px) {
    margin-right: auto;
    width: 100%;
    max-width: 75px;
    img {
      width: 100%;
      height: 100%;
    }
  }
  @media (max-width: 350px) {
    max-width: 65px;
  }
}
.header__top-list {
  display: flex;
  gap: 40px;
  align-items: center;
  font-size: 17px;
  color: var(--color-gray);
  font-weight: 500;
  @media (max-width: 800px) {
    flex-direction: column;
    width: 100%;
    align-items: flex-start;
    justify-content: center;
    height: 100%;
    gap: 30px;
  }
}

.header__top-item {
  &.active {
    position: relative;
    color: var(--color-white);
    &::before {
      content: '';
      position: absolute;
      bottom: -6px;
      width: 90%;
      height: 1px;
      background-color: var(--color-white);
    }
  }
  @media (max-width: 800px) {
    width: 100%;
    color: var(--color-white);
    font-size: 18px;
    border-bottom: 1px solid var(--color-gray);
    padding-bottom: 5px;
    &.active {
      color: var(--color-yellow);
      border-bottom: 1px solid var(--color-yellow);
      &::before {
        display: none;
      }
    }
  }
}

.header__top-nav {
  @media (max-width: 800px) {
    position: fixed;
    width: 100%;
    z-index: 100;
    max-width: 350px;
    transform: translateX(-100%);
    top: 0;
    left: 0;
    bottom: 0;
    transition: all 0.4s;
    padding: 80px 20px 20px;
    height: 100vh;
    box-sizing: border-box;
    overflow-y: auto;
    background-color: var(--color-section-title);
    &::before {
      content: '';
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.5);
      z-index: -1;
      opacity: 0;
      transition: opacity 0.3s;
      pointer-events: none;
    }
  }
}
.header__nav--open {
  transform: translateX(0%);
}
.header__top-btns {
  display: flex;
  gap: 16px;
  .button {
    display: inline-block;
    padding: 10px 30px;
    font-size: 16px;
    font-weight: 800;
    line-height: 24px;
    border: 1px solid var(--color-btn);
    color: var(--color-btn);
    border-radius: 100px;

    @media (max-width: 800px) {
      padding: 5px 10px;
      font-size: 16px;
    }
  }
  .button--color {
    background-color: var(--color-yellow);
    color: var(--color-dark);
    border: 1px solid var(--color-yellow);
  }
  @media (max-width: 400px) {
    gap: 9px;
  }
}
.button--color {
  background-color: var(--color-yellow);
  color: var(--color-dark);
  border: 1px solid var(--color-yellow);
}
.header__body {
  flex: 1 1 auto;

}
.header__body-inner {
  height: 100%;
  display: flex;
  flex-direction: column;
  gap: 20px;
  @media (max-width: 904px) {
    padding: 40px 0;
    gap: 40px;
  }
  @media (max-width: 800px) {
    padding-top: 150px;
  }
}
.header__body-content {
  margin: auto 0;
  display: flex;
  justify-content: space-between;
  gap: 40px;
  position: relative;
  z-index: 1;

  @media (max-width: 904px) {
    flex-direction: column;
  }
}
.header__body-partners {
  margin-bottom: 67px;

  @media (max-width: 1295px) {
    margin-bottom: 0px;
    margin-bottom: 14px;
  }
}

.header__content-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  max-width: 534px;
  @media (max-width: 904px) {
    max-width: 100%;
  }
}
.header__body-title {
  margin-bottom: 70px;
  position: relative;
  z-index: 1;
  &::after {
    content: '';
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 0;
    background-image: url(..//images/icon/star.svg);
    background-repeat: no-repeat;
    background-size: contain;
    width: 38px;
    height: 38px;
    @media (max-width: 904px) {
      display: none;
    }
  }
  h1 {
    font-weight: 800;
    font-size: calc(20px + 30 * ((100vw - 320px) / 1298));
    line-height: 66px;
    letter-spacing: 1%;
    color: var(--color-main-title);
    margin-bottom: 28px;
    @media (max-width: 1478px) {
      line-height: 1.1;
    }
  }
  p {
    width: 100%;
    max-width: 475px;
    font-weight: 500;
    font-size: calc(16px + 7 * ((100vw - 320px) / 1298));
    line-height: 32px;
    letter-spacing: -1.1%;
    margin-bottom: 40px;
    color: var(--color-main-title);
    @media (max-width: 904px) {
      margin-bottom: 20px;
      max-width: 100%;
    }
  }

  @media (max-width: 904px) {
    text-align: center;
    margin-bottom: 40px;
  }
}

.header__body-image {
  width: 100%;
  max-width: 700px;
  position: relative;
  z-index: 1;

  &::after {
    content: '';
    position: absolute;
    top: -25px;
    left: 13px;
    width: 12px;
    height: 12px;
    background-color: var(--color-orange);
    border-radius: 50%;
    @media (max-width: 904px) {
      display: none;
    }
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  @media (max-width: 904px) {
    max-width: 100%;
  }
}
.header__body-btns {
  display: flex;
  gap: 10px;
  .button {
    padding: 13px 35px;
    font-size: 17px;
    font-weight: 800;
    line-height: 24px;
    border: 1px solid var(--color-yellow);
    background-color: var(--color-yellow);
    color: var(--color-dark);
    border-radius: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    @media (max-width: 1000px) {
      padding: 5px 16px;
    }
    @media (max-width: 461px) {
      padding: 3px 10px;
      font-size: 15px;
    }
  }
  .button--play {
    background-color: transparent;
    border: none;
    color: var(--color-white);
  }

  @media (max-width: 904px) {
    justify-content: center;
  }
}

.header__body-list {
  display: flex;
  gap: 32px;
  @media (max-width: 1012px) {
    justify-content: space-between;
  }
}
.header__body-item {
  width: 100%;
  max-width: calc(100% / 3 - 32px);
  text-align: center;
  display: flex;
  flex-direction: column;
  gap: 13px;
  span {
    color: var(--color-yellow);
    font-size: calc(24px + 18 * ((100vw - 320px) / 1298));
    font-weight: 700;
  }
  p {
    text-wrap: nowrap;
    font-size: calc(14px + 4 * ((100vw - 320px) / 1298));
    font-weight: 500;
    color: var(--color-white);
  }
}
.header__partners-list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 40px;
  @media (max-width: 1295px) {
    overflow-x: auto;
    white-space: nowrap;
    scroll-behavior: smooth;
    -ms-overflow-style: none;
    scrollbar-width: none; //    Firefox

    &::-webkit-scrollbar {
      display: none; //    Chrome, Safari
    }
  }
}
.header__partners-item {
  width: auto;
  height: 36px;
  max-width: calc(100% / 6 - 40px);

  @media (max-width: 1295px) {
    flex: 0 0 auto;
    width: auto;
    max-width: 160px;
  }
  @media (max-width: 612px) {
    max-width: 100px;
  }
  img {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
}

.main {
  background-color: var(--color-main-title);
}

.about-section {
  padding-top: 140px;
  padding-bottom: 60px;

  @media (max-width: 790px) {
    padding-top: 60px;
    padding-bottom: 30px;
  }
}

.about__section-inner {
  display: flex;
  justify-content: space-between;
  gap: 40px;
  @media (max-width: 838px) {
    flex-direction: column-reverse;
  }
}
.about__section-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;

  @media (max-width: 838px) {
    text-align: center;
    align-items: center;
  }
}
.about__section-title {
  margin-bottom: 40px;

  @media (max-width: 532px) {
    margin-bottom: 20px;
  }
}
.about__section-image {
  position: relative;
  z-index: 1;
  border-radius: 10px;
  width: 100%;
  max-width: 620px;
  &::before {
    position: absolute;
    content: '';
    bottom: -50px;
    left: -50px;
    background-image: url(..//images/icon/ball.svg);
    width: 182px;
    height: 182px;
    z-index: -1;
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 10px;
  }

  @media (max-width: 838px) {
    max-width: 100%;
    &::before {
      bottom: -20px;
    }
  }
}

.choose-section {
  padding: 60px 0;

  @media (max-width: 790px) {
    padding-top: 40px;
    padding-bottom: 40px;
  }
}

.choose__section-inner {
  display: flex;
  justify-content: space-between;
  gap: 40px;
  @media (max-width: 838px) {
    flex-direction: column;
    gap: 30px;
  }
}

.choose__section-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  @media (max-width: 838px) {
    text-align: center;
    align-items: center;
  }
}

.choose__section-title {
  margin-bottom: 40px;
  @media (max-width: 532px) {
    margin-bottom: 20px;
  }
}

.choose__section-image {
  width: 100%;
  max-width: 624px;
  position: relative;
  z-index: 1;

  &::before {
    position: absolute;
    content: '';
    top: -50px;
    right: -50px;
    background-image: url(..//images/icon/ball.svg);
    width: 182px;
    height: 182px;
    z-index: -1;
    @media (max-width: 838px) {
      top: -20px;
    }
  }

  @media (max-width: 838px) {
    max-width: 100%;
  }
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.choose__section-list {
  display: flex;
  flex-direction: column;
  gap: 24px;
}
.choose__section-item {
  width: 100%;
  max-width: 449px;
  display: flex;
  align-items: center;
  gap: 24px;
}
.choose__item-img {
  padding: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--color-section-title);
  border-radius: 50%;
  @media (max-width: 838px) {
    padding: 13px;
  }
}
.choose__item-content {
  h3 {
    font-weight: 600;
    font-size: calc(17px + 3 * ((100vw - 320px) / 1298));
    margin-bottom: 6px;
    color: var(--color-section-title);
    line-height: 1.2;
  }
  p {
    font-size: calc(15px + 2 * ((100vw - 320px) / 1298));
    color: var(--color-section-parag);
    line-height: 1.2;
  }

  @media (max-width: 838px) {
    text-align: start;
  }
}

.service-section {
  padding: 60px 0;

  @media (max-width: 790px) {
    padding-top: 40px;
    padding-bottom: 40px;
  }
}

.service__section-inner {
  display: flex;
  flex-direction: column;
  gap: 80px;
  align-items: center;
  @media (max-width: 838px) {
    gap: 40px;
  }
}
.service__section-title {
  width: 100%;
  max-width: 665px;
  text-align: center;
}

.service__section-list {
  display: flex;
  gap: 30px;
  flex-wrap: wrap;
  justify-content: space-between;
}
.service__section-item {
  width: 100%;
  padding: 28px;
  width: 100%;
  max-width: calc(100% / 4 - 30px);
  background-color: var(--color-white);
  border-radius: 10px;

  @media (max-width: 1034px) {
    max-width: calc(100% / 2 - 30px);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
  }
  @media (max-width: 685px) {
    max-width: calc(100%);
  }

  h3 {
    font-weight: 600;
    font-size: calc(17px + 4 * ((100vw - 320px) / 1298));
    margin-bottom: 6px;
    color: var(--color-section-title);
    line-height: 1.2;
    width: 100%;
    max-width: 165px;
    margin-bottom: 12px;
    @media (max-width: 1034px) {
      max-width: 100%;
    }
  }
  p {
    font-size: calc(15px + 3 * ((100vw - 320px) / 1298));
    color: var(--color-section-parag);
    line-height: 1.2;
  }
}
.service__item-image {
  padding: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--color-section-title);
  border-radius: 50%;
  width: 58px;
  height: 58px;
  margin-bottom: 24px;
}
.ready-section {
  padding: 60px 0;

  @media (max-width: 790px) {
    padding-top: 40px;
    padding-bottom: 40px;
  }
}
.swiper-slide {
  display: block;
  width: 100%;
  object-fit: cover;
  border-radius: 24px;
  .swiper__slide-image img {
    border-radius: 24px;
    width: 100%;
    max-height: 690px;
  }
}
.swiper__slide-image {
  margin-bottom: 30px;
}
.swiper__slide-text {
  h3 {
    font-weight: 600;
    font-size: calc(17px + 12 * ((100vw - 320px) / 1298));
    margin-bottom: 6px;
    color: var(--color-section-title);
    line-height: 1.2;
    margin-bottom: 20px;
  }
  p {
    font-size: calc(15px + 3 * ((100vw - 320px) / 1298));
    color: var(--color-section-parag);
    line-height: 1.2;
    margin-bottom: 16px;
  }
}
.ready__section-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 40px;
  margin-bottom: 58px;

  @media (max-width: 1115px) {
    align-items: flex-end;
  }
  @media (max-width: 623px) {
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin-bottom: 30px;
    gap: 20px;
  }
}

.ready__section-title {
  width: 100%;
  max-width: 540px;
}

.ready__section-btns {
  display: flex;
  align-items: center;
  gap: 16px;
}

.arrow {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  img {
    width: 100%;
    height: 100%;
  }
}

.arrow--left {
  transform: rotate(180deg);
}

.swiper-btn {
  font-weight: 600;
  font-size: 15px;
  display: flex;
  align-items: center;
  gap: 10px;
  img {
    width: 16px;
    height: 11px;
  }
}

.testimonial-section {
  padding-top: 60px;
  padding-bottom: 90px;

  @media (max-width: 790px) {
    padding-top: 40px;
    padding-bottom: 60px;
  }
}
.testimonial__section-inner {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}
.testimonial__section-title {
  width: 100%;
  max-width: 540px;
  margin-bottom: 80px;
  text-align: center;
  @media (max-width: 623px) {
    margin-bottom: 30px;
  }
}
.swiper.my-coverflow-slider {
  width: 100%;
  overflow: hidden;
}
.my-coverflow-slider .swiper-slide {
  border-radius: 12px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: stretch;
  background: #fff;
  transition: all 0.4s ease;
  padding: 20px 40px;
  border-radius: 20px;
  height: auto;

  @media (max-width: 967px) {
    padding: 15px 30px;
  }
}

.my-coverflow-slider .swiper-wrapper {
  height: 100% !important;
  align-items: stretch !important;
}
.swiper__coverflow-image {
  width: 60px;
  height: 60px;
  margin-bottom: 16px;
  img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
  }
}
.swiper__coverflow-email {
  text-align: center;
  margin-bottom: 20px;
  p {
    font-size: 16px;
    font-weight: 600;
    color: var(--color-dark);
    text-align: center;
    margin-bottom: 6px;
  }
  span {
    color: var(--color-section-parag);
    font-size: 15px;
    text-align: center;
    display: inline-block;
  }
}
.swiper__coverflow-comments {
  color: var(--color-section-title);
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  img {
    width: 20px;
    height: 20px;
  }
  p {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    gap: 11px;
    text-align: center;
    font-size: calc(14px + 1 * ((100vw - 320px) / 1298));
    line-height: 1.1;
  }
}
.swiper-pagination {
  bottom: -35px !important;
}

.testimonial__section-inner .swiper-pagination-bullet-active {
  background: var(--color-dark) !important;
  transform: scale(1.2) !important;
}

.contact-section {
  padding: 60px 0;
  background-color: var(--color-white);
  @media (max-width: 790px) {
    padding-top: 40px;
    padding-bottom: 40px;
  }
}

.contact__section-inner {
  display: flex;
  justify-content: space-between;
  gap: 40px;

  @media (max-width: 896px) {
    flex-direction: column;
  }
}
.contact__section-title {
  width: 100%;
  max-width: 550px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  h2 {
    font-size: calc(20px + 13 * ((100vw - 320px) / 1298));
  }

  @media (max-width: 896px) {
    max-width: 100%;
    text-align: center;
    justify-content: center;
    align-items: center;
  }
}
.contact__section-text {
  margin-bottom: 24px;
}

.contact__section-list {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.contact__section-item a {
  display: inline-flex;
  gap: 15px;
  font-weight: 600;
  font-size: calc(13px + 2 * ((100vw - 320px) / 1298));
  color: var(--color-section-parag);
}
.contact__section-form {
  width: 100%;
  max-width: 620px;
  background-color: var(--color-dark);
  color: var(--color-white);
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;

  @media (max-width: 896px) {
    max-width: 100%;
  }

  form {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    padding: 60px;
    gap: 24px;
  }
}
.form__contact-box {
  display: flex;
  flex-direction: column;
  gap: 10px;
  width: 100%;
  input[type='email'],
  input[type='text'],
  textarea {
    width: 100%;
    resize: none;
    background-color: transparent;
    border-bottom: 1px solid var(--color-white);
    color: var(--color-white);
    padding: 8px 0;
    font-weight: 600;
    font-size: calc(14px + 2 * ((100vw - 320px) / 1298));
  }
}
textarea {
  resize: none;
  max-height: 50px;
  overflow-y: hidden;
}

.label {
  font-size: calc(14px + 1 * ((100vw - 320px) / 1298));
}

.footer {
  background-color: var(--color-dark);
  padding-top: 78px;
  padding-bottom: 57px;
}

.footer__inner {
  display: flex;
  justify-content: space-between;
  color: var(--color-white);
  gap: 50px;

  @media (max-width: 1200px) {
    flex-direction: column;
  }
}

.footer__content {
  display: flex;
  flex-direction: column;
  gap: 20px;
  width: 100%;
  max-width: 330px;
  p {
    font-size: calc(13px + 2 * ((100vw - 320px) / 1298));
    line-height: 1.2;
  }
  @media (max-width: 1200px) {
    max-width: 100%;
    text-align: center;
    justify-content: center;
    align-items: center;
  }
}

.footer__content-list {
  display: flex;
  gap: 16px;
}
.footer__content-item {
  width: 100%;
  max-width: 27px;
  img {
    width: 100%;
    height: 100%;
  }
}

.footer__nav {
  width: 100%;
  max-width: 800px;
  display: flex;
  justify-content: space-between;
  gap: 30px;
  @media (max-width: 1200px) {
    max-width: 100%;
    flex-wrap: wrap;
    gap: 45px;
  }
}

.footer__nav-links {
  display: flex;
  flex-direction: column;
  gap: 24px;
  h3 {
    font-size: calc(17px + 3 * ((100vw - 320px) / 1298));
    font-weight: 600;
  }
}
.footer__nav-list {
  display: flex;
  flex-direction: column;
  gap: 13px;
}
.footer__nav-item {
  font-size: calc(14px + 2 * ((100vw - 320px) / 1298));
  color: var(--color-gray);
  transition: all 0.3s ease;
  &:hover {
    color: var(--color-white);
  }
}
.footer__nav-contact {
  display: flex;
  flex-direction: column;
  gap: 24px;
  h3 {
    font-size: calc(17px + 3 * ((100vw - 320px) / 1298));
    font-weight: 600;
  }
}
.footer__contact-list {
  display: flex;
  flex-direction: column;
  gap: 13px;
}
.footer__contact-item {
  a {
    display: inline-flex;
    gap: 10px;
    font-weight: 600;
    font-size: calc(13px + 2 * ((100vw - 320px) / 1298));
    color: var(--color-gray);
    transition: all 0.3s ease;
    &:hover {
      color: var(--color-white);
    }
  }
}
