@mixin font-face($family, $filename: $family, $weight: 400, $style: normal) {
  @font-face {
    font-family: "#{$family}";
    font-display: swap;
    src: url("..//fonts/#{$filename}.woff2") format("woff2");
    font-weight: $weight;
    font-style: $style;
  }
}

@include font-face("ITC Avant Garde Gothic Std", "..//fonts/ITCAvantGardeStd-Bk.woff2", 400, normal);
@include font-face("ITC Avant Garde Gothic Std", "..//fonts/ITCAvantGardeStd-BkObl.woff2", 400, italic);
@include font-face("ITC Avant Garde Gothic Std", "..//fonts/ITCAvantGardeStd-Bold.woff2", 700, normal);
@include font-face("ITC Avant Garde Gothic Std", "..//fonts/ITCAvantGardeStd-BoldObl.woff2", 700, italic);
@include font-face("ITC Avant Garde Gothic Std", "..//fonts/ITCAvantGardeStd-Demi.woff2", 500, normal);
@include font-face("ITC Avant Garde Gothic Std", "..//fonts/ITCAvantGardeStd-XLt.woff2", 300, normal);
@include font-face("ITC Avant Garde Gothic Std", "..//fonts/ITCAvantGardeStd-XLtObl.woff2", 300, italic);
