:root {
  --color-orange: #edb32d;
  --color-yellow: #f0e714;
  --color-white: #ffffff;
  --color-gray: #bab9b9;
  --color-btn: #cecece;
  --color-main-title: #f3f4f6;
  --color-yellow-second: rgba(240, 231, 20, 0.502);
  --color-drop-shadow: rgba(119, 134, 75, 0.149);
  --color-dark: #101c26;
  --color-section-title: #24272e;
  --color-section-parag: #555c68;
  --color-section-box: rgba(240, 231, 20, 0.2);
}

.wrapper {
  width: 100%;
  min-height: 100%;
  overflow: hidden;
  font-size: 15px;
}

.container {
  max-width: 1298px;
  margin: 0 auto;
  padding: 0 10px;
  height: 100%;
}

.image--border {
  border-radius: 10px;
}
.image--border img {
  border-radius: 10px;
}

.section--box {
  color: var(--color-section-title);
  font-size: calc(13px + 3 * (100vw - 320px) / 1298);
  font-weight: 400;
  background-color: var(--color-section-box);
  border-radius: 1000px;
  padding: 10px 16px;
  text-transform: uppercase;
  display: inline-block;
  margin-bottom: 24px;
}

.section--title {
  color: var(--color-section-title);
  font-size: calc(18px + 19 * (100vw - 320px) / 1298);
  font-weight: 600;
  line-height: 1.1;
  margin-bottom: 16px;
}

.section--text {
  color: var(--color-section-parag);
  font-weight: 400;
  font-size: calc(15px + 5 * (100vw - 320px) / 1298);
  line-height: 1.3;
}

.button {
  display: flex;
  justify-content: center;
  align-items: center;
  background: var(--color-section-title);
  padding: 15px 30px;
  font-size: 15px;
  font-weight: 600;
  color: var(--color-yellow);
  gap: 6px;
  border-radius: 100px;
}
@media (max-width: 740px) {
