}

.burger {
  display: none;
}
@media (max-width: 800px) {
  .burger {
    margin-right: 10px;
    background: var(--color-yellow);
    box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;
    top: 25px;
    left: 20px;
    z-index: 150;
    height: 40px;
    width: 40px;
    border-radius: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: all 0.3s;
  }
  .burger span {
    display: block;
    width: 25px;
    height: 3px;
    background-color: #fff;
    border-radius: 3px;
    margin: 4px 0;
  }
  .burger::before, .burger::after {
    content: "";
    display: block;
    width: 25px;
    height: 3px;
    background-color: #fff;
    border-radius: 3px;
  }
}
.burger.burger__active span {
  display: none;
}
.burger.burger__active.burger::before {
  position: absolute;
  transform: rotate(45deg);
  box-shadow: 0 6px 0 rgba(0, 0, 0, 0);
  transition: top 0.3s, -webkit-box-shadow 0.15s, -webkit-transform 0.3s;
  transition: box-shadow 0.15s, top 0.3s, transform 0.3s;
  transition: box-shadow 0.15s, top 0.3s, transform 0.3s, -webkit-box-shadow 0.15s, -webkit-transform 0.3s;
}
.burger.burger__active.burger::after {
  position: absolute;
  transform: rotate(-45deg);
  transition: top 0.3s, -webkit-box-shadow 0.15s, -webkit-transform 0.3s;
  transition: box-shadow 0.15s, top 0.3s, transform 0.3s;
  transition: box-shadow 0.15s, top 0.3s, transform 0.3s, -webkit-box-shadow 0.15s, -webkit-transform 0.3s;
}

